<template>
  <section>
    <b-collapse class="card" :open.sync="isOpen">
      <div slot="trigger" class="card-header">
        <p class="card-header-title" :title="room.description">
          {{room.name}}&nbsp;
          <a @click="openEditRoomModal">edit</a>
        </p>
        
        <a class="card-header-icon">
          <b-icon :icon="isOpen ?
              'menu-down' : 'menu-up'">
          </b-icon>
        </a>
      </div>
      <div class="panel-block">
        <div class="columns is-multiline">
          <div class="column is-one-quarter" v-for="(module, index) in room.modules" :key="module.title">
            <module-card :module="module" />
          </div>
        </div>
      </div>
    </b-collapse>
    <b-modal :active.sync="isEditRoomModalActive" has-modal-card>
      <edit-room-form :room="Object.assign({}, room)" />
    </b-modal>
  </section>
</template>

<script>
  import ModuleCard from './ModuleCard'
  import EditRoomForm from './EditRoomForm'

  export default {
    props: ['room'],
    components: { ModuleCard, EditRoomForm },
    data () {
      return {
        isOpen: true,
        isEditRoomModalActive: false
      }
    },
    methods: {
      openEditRoomModal () {
        this.isEditRoomModalActive = true
        this.isOpen = !this.isOpen
      }
    }
  }
</script>

<style scoped>
.panel-block .columns {
  width: 100%;
}
.card-header-title a {
  opacity: 0.3;
  transition: opacity .5s;
  font-size: .9rem;
}
.card-header-title a:hover {
  opacity: 1;
}
</style>



