<template>
  <form @submit.prevent="addAssociate">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Add a associate</p>
      </header>
      <section class="modal-card-body">
        <b-field label="Username">
          <b-input v-model="associate.username" placeholder="Username" required>
          </b-input>
        </b-field>
        <b-field label="Email">
          <b-input v-model="associate.email" placeholder="Email" required>
          </b-input>
        </b-field>
        <b-field label="First Name">
          <b-input v-model="associate.firstName" placeholder="First Name" required>
          </b-input>
        </b-field>
        <b-field label="Last Name">
          <b-input v-model="associate.lastName" placeholder="Last Name" required>
          </b-input>
        </b-field>
        <b-field label="Password">
          <b-input v-model="associate.password" required type="password" placeholder="Your password" password-reveal></b-input>
        </b-field>
        <b-field label="Confirmation">
          <b-input v-model="associate.confirmation" required type="password" placeholder="Confirm your password" password-reveal></b-input>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">Close</button>
        <button class="button is-primary">Add</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  // import { mapState } from 'vuex'
  export default {
    name: 'newAssociateForm',
    data () {
      return {
        associate: {
          username: '',
          email: '',
          firstName: '',
          lastName: '',
          password: '',
          confirmation: ''
        }
      }
    },
    methods: {
      addAssociate () {
        const loadingComponent = this.$loading.open()

        const form = {
          username: this.associate.username,
          email: this.associate.email,
          first_name: this.associate.firstName,
          last_name: this.associate.lastName,
          password: this.associate.password
        }

        api.addAssociate(form).then((result) => {
          console.log(result)
          loadingComponent.close()
          this.$parent.close()
          this.$toast.open({
            message: 'Associate sucefully registered!',
            type: 'is-success'
          })
        }).catch((error) => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>



