export default {
  moduleState: (state) => (identifier) => {
    const ms = state.modulesState.find((moduleState) => moduleState.identifier === identifier)

    return ms ? ms.state : false
  },
  roomById: (state) => (id) => {
    return state.rooms.find((room) => room.id === id)
  }
}



