<template>
  <section>
    <header-title title="Dashboard" :subtitle="`Feeling good, ${this.user.firstName ? this.user.firstName : '@' + this.user.name}?`"/>
    <div class="container">
      <room v-for="room in rooms" :key="room.id" :room="room" :header="room.name" :modules="room.modules"></room>
    </div>
    <b-loading :active.sync="isLoading"></b-loading>
  </section>
</template>

<script>
import 'paho-mqtt'
import HeaderTitle from '../components/HeaderTitle'
import Room from '../components/Room'
import { mapState } from 'vuex'

window.LunaClient = new window.Paho.MQTT.Client('iot.eclipse.org', 443, 'luna-' + new Date())

export default {
  name: 'dashboard-view',
  components: { HeaderTitle, Room },
  data () {
    return {
      loadingModules: true,
      isLoading: false
    }
  },
  computed: mapState(['user', 'rooms', 'modulesState', 'changedModule']),
  watch: {
    changedModule (module, oldModule) {
      if (module.state !== oldModule.state) {
        let message = new window.Paho.MQTT.Message('1')
        message.destinationName = `m/${module.identifier}/${(module.state ? 101 : 102)}`
        window.LunaClient.send(message)
        console.info('↗ MQTT:', message.destinationName, message.payloadString)
      }
    }
  },
  beforeMount: function () {
    const vm = this
    vm.isLoading = true
    window.LunaClient.onMessageArrived = (message) => {
      let identifier = message.destinationName.split('/')[1]
      console.info('↙ MQTT:', message.destinationName, message.payloadString)
      this.$store.dispatch('UPDATE_MODULE_STATE', { identifier, state: message.payloadString === '1', remote: true })
    }
    window.LunaClient.connect({
      useSSL: true,
      onSuccess: () => {
        vm.$store.dispatch('FETCH_USER_ROOMS').then((rooms) => {
          rooms.forEach(room => {
            room.modules.forEach((module) => {
              window.LunaClient.subscribe(`m/${module.identifier}/1/1`)
            })
          })
          vm.isLoading = false
        }).catch((error) => {
          console.error(error)
          vm.isLoading = false
        })
      }
    })
  },
  beforeDestroy: function () {
    window.LunaClient.disconnect()
  }
}
</script>

<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



