<template>
  <section class="card">
    <header class="card-header">
      <p class="card-header-title">
        {{module.name}}
      </p>
      <a @click="openEditModuleModal" class="card-header-icon" aria-label="more options">
        <span class="icon">
          <b-icon icon="pencil"></b-icon>
        </span>
      </a>
    </header>
    <div class="card-content">
      <div class="content">
        <div class="field">
            <b-switch v-model="switchState" size="">{{getText()}}</b-switch>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ['module'],
  methods: {
    getDescription: function () {
      return this.switchState === true ? 'Turned on' : 'Turned off'
    },
    getText: function () {
      return this.switchState === true ? 'on' : 'off'
    },
    openEditModuleModal () {
      this.$store.commit('SET_SELECTED_EDIT_MODULE', this.module)
    }
  },
  computed: {
    switchState: {
      set: function (state) {
        this.$store.dispatch('UPDATE_MODULE_STATE', { identifier: this.module.identifier, state: state, remote: false })
      },
      get: function () {
        return this.$store.getters.moduleState(this.module.identifier)
      }
    }
  }
}
</script>


<style>
</style>



