<template>
  <section>
    <header-title title="Profile"/>
    <div id="profile-view" class="container">
      <b-notification :active.sync="updateError" type="is-danger">
        Something goes wrong!
      </b-notification>
      <b-notification :active.sync="updateSuccess" type="is-success">
        Profile updated successfully!
      </b-notification>
      <b-notification :active="!this.user.firstName" type="is-info" has-icon>
        Please, update your profile to get access to Luna!
      </b-notification>

      <form @submit.prevent="updateProfile">
        <b-field grouped>
          <b-field label="Username" expanded>
            <b-input disabled v-model="form.name" size="is-medium" required placeholder="Your username"></b-input>
          </b-field>
          <b-field label="Email" expanded>
            <b-input v-model="form.email" type="email" required size="is-medium" placeholder="Your email"></b-input>
          </b-field>
        </b-field>

        <b-field grouped>
          <b-field label="First Name" expanded>
            <b-input v-model="form.firstName" required size="is-medium" placeholder="Your first name"></b-input>
          </b-field>
          <b-field label="Last Name" expanded>
            <b-input v-model="form.lastName" required size="is-medium" placeholder="Your last name"></b-input>
          </b-field>
        </b-field>
  
        <button type="submit" class="button is-primary is-medium">
          Update Profile
        </button>
      </form>
      <br>
      <b-loading :active.sync="isLoading"></b-loading>
    </div>
  </section>
</template>

<script>
import HeaderTitle from '../components/HeaderTitle'
import { mapState } from 'vuex'

export default {
  name: 'profile-view',
  components: { HeaderTitle },
  data () {
    return {
      form: {
        name: this.$store.state.user.name,
        email: this.$store.state.user.email,
        firstName: this.$store.state.user.firstName,
        lastName: this.$store.state.user.lastName
      },
      isLoading: false,
      updateSuccess: false,
      updateError: false
    }
  },
  computed: mapState(['user']),
  beforeMount: function () {
    const vm = this
    vm.isLoading = true
    vm.$store.dispatch('FETCH_PROFILE', vm.$store.state.user.name).then((response) => {
      vm.isLoading = false
      vm.form = Object.assign({}, vm.$store.state.user)
    }).catch((error) => {
      console.log(error)
      vm.isLoading = false
    })
  },
  methods: {
    updateProfile () {
      const vm = this
      vm.isLoading = true
      vm.form.username = vm.form.name
      vm.$store.dispatch('UPDATE_PROFILE', { username: vm.form.username, newProfile: vm.form }).then((response) => {
        vm.isLoading = false
        vm.form = vm.$store.state.user
        vm.updateSuccess = true
      }).catch((error) => {
        console.log(error)
        vm.isLoading = false
        vm.updateError = true
      })
    }
  }
}
</script>

<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



