<template>
  <div id="login-view" class="container">
    <h1 class="title">Luna</h1>
    <p class="subtitle">
      Access your account
    </p>
    <b-notification :active.sync="loginError" type="is-danger">
      Unable to log in with provided credentials
    </b-notification>
    <form @submit.prevent="login">
      <b-field>
        <b-input v-model="form.username" required size="is-medium" placeholder="Your username"></b-input>
      </b-field>
      <b-field>
        <b-input v-model="form.password" required size="is-medium" type="password" placeholder="Your password" password-reveal></b-input>
      </b-field>
      <button class="button is-primary is-medium is-fullwidth">
        Sign In
      </button>
      <div class="is-clearfix"></div>
      <hr class="">
      <a class="button is-default is-link">
        Problems with your credentials?
      </a>
    </form>
    <br>
    <router-link to="/signup" class="button is-default is-link">
      Don't have an account?
    </router-link>
    <b-loading :active.sync="isLoading"></b-loading>
  </div>
</template>

<script>
export default {
  name: 'login-view',
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      isLoading: false,
      loginError: false
    }
  },
  methods: {
    login () {
      this.isLoading = true
      this.$store.dispatch('LOGIN', this.form).then((response) => {
        this.$store.dispatch('FETCH_PROFILE', this.form.username).then(() => {
          this.$router.push('/')
        })
      }).catch((error) => {
        console.log(error)
        this.isLoading = false
        this.loginError = true
      })
    }
  }
}
</script>

<style scoped>
  #login-view {
    margin: 2em auto;
    width: 25em;
  }
</style>



