<template>
  <form @submit.prevent="registerModule">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Add a module</p>
      </header>
      <section class="modal-card-body">
        <b-field label="Identifier" :type="fieldType" :message="getIdentifierMessage()">
          <b-input
            v-model="module.identifier"
            placeholder="Identifier" maxlength="8" icon="magnify" 
            @input="checkModuleIsValid"
            :loading="loadingModule"
            ></b-input>
        </b-field>
        <b-field label="Name">
          <b-input :disabled="!isModuleValid" v-model="module.name" placeholder="Name of the module" required>
          </b-input>
        </b-field>
        <b-field label="Room">
          <b-autocomplete
              v-model="module.roomName"
              placeholder="Room for the module"
              :data="this.rooms"
              field="name"
              required
              :disabled="!isModuleValid"
              @select="option => module.room = option ? option.id : ''">
          </b-autocomplete>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">Close</button>
        <button :disabled="!isModuleValid" class="button is-primary">Register</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  import { mapState } from 'vuex'
  export default {
    name: 'newModuleForm',
    computed: {
      ...mapState(['rooms'])
    },
    data () {
      return {
        module: {
          id: '',
          identifier: '',
          type: '',
          typeName: '',
          room: '',
          roomName: '',
          name: ''
        },
        isModuleValid: false,
        loadingModule: false,
        fieldType: ''
      }
    },
    methods: {
      getIdentifierMessage () {
        switch (this.fieldType) {
          case 'is-success':
            return `You can register this module (${this.module.typeName})`
          case 'is-danger':
            return `You can't add this module`
          case 'is-warning':
            return 'Try again later'
          default:
            return ''
        }
      },
      checkModuleIsValid () {
        const vm = this
        if (vm.module.identifier.length === 8) {
          vm.loadingModule = true
          api.checkIfModuleIsValid(vm.module.identifier).then((result) => {
            vm.module.type = result.data.module_type
            vm.module.id = result.data.id
            api.getModuleType(vm.module.type).then((result) => {
              const data = result.data
              vm.fieldType = 'is-success'
              vm.loadingModule = false
              vm.isModuleValid = true
              vm.module.typeName = data.name
              vm.module.name = data.name
            })
          }).catch((error) => {
            if (error.response.status === 404) {
              vm.fieldType = 'is-danger'
            } else {
              vm.fieldType = 'is-warning'
            }
            vm.loadingModule = false
            vm.isModuleValid = false
          })
        } else {
          vm.fieldType = ''
          vm.loadingModule = false
          vm.isModuleValid = false
        }
      },
      registerModule () {
        const loadingComponent = this.$loading.open()

        const moduleRegistered = (response) => {
          this.$store.dispatch('FETCH_USER_ROOMS').then((rooms) => {
            loadingComponent.close()
            this.$parent.close()
            this.$toast.open({
              message: 'Module sucefully registered!',
              type: 'is-success'
            })
          })
        }

        const handleError = (error) => {
          console.log(error)
          loadingComponent.close()
          this.$toast.open({
            message: 'Module not registered!',
            type: 'is-danger'
          })
        }

        let form = {
          name: this.module.name,
          is_active: true,
          room: this.module.room,
          identifier: this.module.identifier,
          module_type: this.module.type
        }

        if (!this.module.room) {
          api.addRoom({ name: this.module.roomName }).then((resolve) => {
            form.room = resolve.data.id
            api.registerModule(this.module.identifier, form).then(moduleRegistered).catch(handleError)
          }).catch(handleError)
        } else {
          api.registerModule(this.module.identifier, form).then(moduleRegistered).catch(handleError)
        }
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>


