<template>
  <b-table
    :data="associates"
    :bordered="true"
    :striped="false"
    :narrowed="false"
    :hoverable="false"
    :mobile-cards="true">

    <template scope="props">
      <b-table-column label="First Name">
        {{ props.row.first_name }}
      </b-table-column>

      <b-table-column label="Last Name">
        {{ props.row.last_name }}
      </b-table-column>

      <b-table-column label="Username">
        {{ props.row.username }}
      </b-table-column>

      <b-table-column label="Email">
        {{ props.row.email }}
      </b-table-column>

      <b-table-column label="Ações">
        Editar | Excluir
      </b-table-column>
    </template>

    <template slot="empty">
        <section class="section">
            <div class="content has-text-grey has-text-centered">
                <p>Nothing here.</p>
            </div>
        </section>
    </template>
  </b-table>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: 'associatesTable',
    props: ['associates'],
    computed: {
      ...mapState(['rooms'])
    },
    methods: {
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>


