<template>
  <form @submit.prevent="editRoom">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Edit {{room.name}}</p>
      </header>
      <section class="modal-card-body">
        <b-field label="Name">
          <b-input v-model="room.name" placeholder="Name of the room" required></b-input>
        </b-field>
        <b-field label="Description">
          <b-input v-model="room.description" placeholder="Description for the room">
          </b-input>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">Close</button>
        <button class="button is-primary">Save</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  import { mapState } from 'vuex'
  export default {
    name: 'editRoomForm',
    props: ['room'],
    computed: {
      ...mapState(['rooms'])
    },
    methods: {
      editRoom () {
        const loadingComponent = this.$loading.open()

        let form = {
          id: this.room.id,
          name: this.room.name,
          description: this.room.description
        }

        api.updateRoom(form.id, form).then((resolve) => {
          this.$store.dispatch('FETCH_USER_ROOMS').then((rooms) => {
            loadingComponent.close()
            this.$parent.close()
            this.$toast.open({
              message: 'Room sucefully updated!',
              type: 'is-success'
            })
          })
        }).catch((error) => {
          console.log(error)
          loadingComponent.close()
          this.$toast.open({
            message: 'Room not updated!',
            type: 'is-danger'
          })
        })
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>


