import axios from 'axios'

let client

const BASE_URL = process.env.API_URL || 'http://localhost:8000'

const getClient = () => {
  if (!client) {
    const user = JSON.parse(localStorage.getItem('user'))
    const token = user ? user.token : ''
    client = axios.create({
      baseURL: BASE_URL,
      headers: {
        'Authorization': token ? `JWT ${token}` : ''
      }
    })
  }
  return client
}

const setToken = (token) => {
  Object.assign(client.defaults, {
    headers: {
      'Authorization': token ? `JWT ${token}` : ''
    }
  })
}

const loadRooms = () => getClient().get('/api/v1/rooms/')

const loadModules = () => getClient().get('/api/v1/modules/')

const loadAssociates = () => getClient().get('/api/v1/associates/')

const getModuleType = (uuid) => getClient().get(`/api/v1/module_types/${uuid}/`)

const checkIfModuleIsValid = (identifier) => getClient().get(`/api/v1/modules_pending/${identifier}/`)

const updateModule = (uuid, form) => getClient().put(`/api/v1/modules/${uuid}/`, form)

const updateRoom = (uuid, form) => getClient().put(`/api/v1/rooms/${uuid}/`, form)

const registerModule = (identifier, form) => getClient().put(`/api/v1/modules_pending/${identifier}/`, form)

const addRoom = (form) => getClient().post(`/api/v1/rooms/`, form)

const addAssociate = (form) => getClient().post(`/api/v1/associates/`, form)

const registerUser = (form) => getClient().post('/api/v1/users/', form)

const loginUser = (form) => getClient().post('/api/auth/token/', form).then(response => {
  setToken(response.data.token || '')
  return response
})

const loadProfile = (username) => getClient().get(`/api/v1/profile/${username}`)

const handleSpeech = (text, lang) => getClient().post('/api/v1/speech/', {
  lang: lang,
  text: text
})

const updateProfile = (username, profile) => {
  profile.first_name = profile.firstName
  profile.last_name = profile.lastName
  return getClient().put(`/api/v1/profile/${username}/`, profile)
}

const logoutUser = () => {
  setToken(null)
}

export default {
  loadRooms,
  loadModules,
  getModuleType,
  checkIfModuleIsValid,
  addRoom,
  addAssociate,
  loadAssociates,
  updateModule,
  updateRoom,
  registerModule,
  registerUser,
  loginUser,
  loadProfile,
  updateProfile,
  logoutUser,
  handleSpeech
}



