import api from '../api'
import { parseJwt } from '../utils/jwt'

export default {

  FETCH_USER_ROOMS: ({ commit }) => {
    return new Promise((resolve, reject) => {
      api.loadRooms().then(response => {
        let rooms = response.data.results
        api.loadModules().then(response => {
          const data = response.data.results
          rooms = rooms.map(r => ({ ...r, modules: data.filter(m => m.room === r.id) }))

          commit('SET_ROOMS', rooms)
          resolve(rooms)
        })
      }).catch(error => {
        reject(error)
      })
    })
  },
  UPDATE_MODULE_STATE: ({ commit, state }, moduleState) => {
    let ms = state.modulesState.find((ms) => ms.identifier === moduleState.identifier)

    let action = ms ? 'SET_MODULE_STATE' : 'ADD_MODULE_STATE'

    commit(action, moduleState)
  },
  FETCH_PROFILE: ({ commit }, username) => {
    return new Promise((resolve, reject) => {
      api.loadProfile(username).then(response => {
        commit('SET_PROFILE', response.data)
        resolve(response)
      }).catch(error => {
        reject(error)
      })
    })
  },
  UPDATE_PROFILE: ({ commit }, { username, newProfile }) => {
    return new Promise((resolve, reject) => {
      api.updateProfile(username, newProfile).then(response => {
        commit('SET_PROFILE', response.data)
        resolve(response)
      }).catch(error => {
        reject(error)
      })
    })
  },
  REGISTER: ({ commit }, form) => api.registerUser(form),
  LOGIN: ({ commit, dispatch }, form) => {
    return new Promise((resolve, reject) => {
      api.loginUser(form).then((response) => {
        const data = response.data
        const payload = parseJwt(data.token)
        const user = {
          name: form.username,
          token: data.token,
          isOwner: payload.is_owner,
          email: payload.email
        }
        commit('SET_USER', user)
        resolve(user)
      }).catch(error => {
        reject(error)
      })
    })
  },
  LOGOUT: ({ commit }) => {
    api.logoutUser()
    localStorage.removeItem('user')
    commit('RESET_STORE')
  }
}



