<template>
  <form @submit.prevent="editModule">
    <div class="modal-card">
      <header class="modal-card-head">
        <p class="modal-card-title">Edit {{module.name}}</p>
      </header>
      <section class="modal-card-body">
        <b-field label="Identifier" :type="fieldType">
          <b-input v-model="module.identifier" placeholder="Identifier" maxlength="8" disabled></b-input>
        </b-field>
        <b-field label="Name">
          <b-input v-model="module.name" placeholder="Name of the module" required>
          </b-input>
        </b-field>
        <b-field label="Room">
          <b-autocomplete
              v-model="roomName"
              placeholder="Room for the module"
              :data="this.rooms"
              field="name"
              required
              @select="option => module.room = option ? option.id : ''">
          </b-autocomplete>
        </b-field>
      </section>
      <footer class="modal-card-foot">
        <button class="button" type="button" @click="$parent.close()">Close</button>
        <button class="button is-primary">Save</button>
      </footer>
    </div>
  </form>
</template>

<script>
  import api from '../api'
  import { mapState } from 'vuex'
  export default {
    name: 'editModuleForm',
    props: ['module'],
    computed: {
      ...mapState(['rooms'])
    },
    data () {
      return {
        roomName: this.$store.getters.roomById(this.module.room).name,
        isModuleValid: false,
        loadingModule: false,
        fieldType: ''
      }
    },
    methods: {
      editModule () {
        const loadingComponent = this.$loading.open()

        const moduleUpdated = (response) => {
          this.$store.dispatch('FETCH_USER_ROOMS').then((rooms) => {
            loadingComponent.close()
            this.$parent.close()
            this.$toast.open({
              message: 'Module sucefully updated!',
              type: 'is-success'
            })
          })
        }

        const handleError = (error) => {
          console.log(error)
          loadingComponent.close()
          this.$toast.open({
            message: 'Module not updated!',
            type: 'is-danger'
          })
        }

        let form = {
          name: this.module.name,
          is_active: true,
          room: this.module.room,
          identifier: this.module.identifier,
          module_type: this.module.module_type
        }

        if (!this.module.room) {
          api.addRoom({ name: this.roomName }).then((resolve) => {
            form.room = resolve.data.id
            api.updateModule(this.module.id, form).then(moduleUpdated).catch(handleError)
          }).catch(handleError)
        } else {
          api.updateModule(this.module.id, form).then(moduleUpdated).catch(handleError)
        }
      }
    }
  }
</script>

<style scoped>
  .modal-card {
    width: auto;
    min-height: 600px;
  }
</style>


