<template>
  <form @submit.prevent="register" id="register-view" class="container">
    <b-loading :active.sync="isLoading"></b-loading>
    <h1 class="title">Luna</h1>
    <p class="subtitle">
      Create a new account
    </p>
    <b-field>
      <b-input v-model="form.identifier" required size="is-medium" placeholder="Enter a module identifier"></b-input>
    </b-field>
    <b-field>
      <b-input v-model="form.username" required size="is-medium" placeholder="Your username"></b-input>
    </b-field>
    <b-field>
      <b-input v-model="form.email" required size="is-medium" placeholder="Your email"></b-input>
    </b-field>
    <b-field>
      <b-input v-model="form.password" required size="is-medium" type="password" placeholder="Your password" password-reveal></b-input>
    </b-field>
    <b-field :type="(!passwordMatch ? 'is-danger' : '')"  :message="(!passwordMatch ? 'Password does not match the confirm password.' : '')">
      <b-input v-model="form.confirmation" required size="is-medium" type="password" placeholder="Confirm your password" password-reveal></b-input>
    </b-field>
    <button class="button is-primary is-medium is-fullwidth">
      Sign Up
    </button>
    <div class="is-clearfix"></div>
    <hr class="">
    <router-link to="/signin" class="button is-default is-link">
      Already have an account?
    </router-link>
  </form>
</template>

<script>
export default {
  name: 'register-view',
  data () {
    return {
      form: {
        username: '',
        email: '',
        identifier: '',
        password: '',
        confirmation: ''
      },
      isLoading: false,
      passwordMatch: -1
    }
  },
  methods: {
    register () {
      if (this.form.password !== this.form.confirmation) {
        this.passwordMatch = 0
        return
      }
      this.isLoading = true
      this.$store.dispatch('REGISTER', this.form).then((response) => {
        this.isLoading = false
        this.$router.push('/signin')
      }).catch((error) => {
        console.log(error)
        this.isLoading = false
      })
    }
  }
}
</script>


<style scoped>
  #register-view {
    margin: 2em auto;
    width: 25em;
  }
</style>



