<template>
  <nav class="navbar is-primary" role="navigation" aria-label="main navigation">
    <div class="navbar-brand">
      <router-link class="navbar-item" to="/">
        <strong>Luna</strong>
      </router-link>
      <a @click="startRecognition" class="navbar-item">
        <b-icon icon="microphone"></b-icon>
      </a>
    </div>
    <div class="navbar-menu">
      <div class="navbar-end">

        <a @click="openNewModuleModal" class="navbar-item">
          <b-icon icon="plus"></b-icon>&nbsp;
          Add module
        </a>
        <div class="navbar-item has-dropdown is-hoverable">
          <a class="navbar-link">
            Me
          </a>
          <div class="navbar-dropdown is-right">
            <router-link to="/profile" class="navbar-item">
              Profile
            </router-link>
            <router-link v-if="this.user.isOwner" to="/associates" class="navbar-item">
              Associates
            </router-link>
            <router-link to="/" class="navbar-item">
              Settings
            </router-link>
            <a @click="logout" class="navbar-item">
                Logout
              </a>
          </div>
        </div>
      </div>
    </div>
    <b-modal :active.sync="isNewModuleModalActive" has-modal-card>
      <new-module-form />
    </b-modal>
    <b-modal :active.sync="isEditModuleModalActive" has-modal-card>
      <edit-module-form :module="Object.assign({}, this.$store.state.editSelectedModule)" />
    </b-modal>
  </nav>
</template>

<script>
  import NewModuleForm from './NewModuleForm'
  import EditModuleForm from './EditModuleForm'
  import { mapState } from 'vuex'
  import * as speech from '../utils/speech'
  import api from '../api'

  export default {
    name: 'toolbar',
    data () {
      return {
        isNewModuleModalActive: false
      }
    },
    computed: {
      ...mapState(['user']),
      isEditModuleModalActive: {
        get: function () {
          return this.$store.state.editSelectedModule !== null
        },
        set: function (value) {
          this.$store.commit('SET_SELECTED_EDIT_MODULE', null)
        }
      }
    },
    components: {
      NewModuleForm, EditModuleForm
    },
    methods: {
      logout () {
        this.$store.dispatch('LOGOUT')
        this.$router.push('/signin')
      },
      openNewModuleModal () {
        this.isNewModuleModalActive = true
      },
      startRecognition () {
        speech.recognition((text) => {
          api.handleSpeech(text, 'en').then(({data}) => {
            console.info('Feedback:', data)
            if (!data.result.actionIncomplete) {
              if (data.result.parameters.room && data.result.parameters.module && data.result.parameters.command) {
                const room = this.$store.state.rooms.find(r => r.name.toLowerCase() === data.result.parameters.room)
                const module = room.modules.find(m => m.name.toLowerCase() === data.result.parameters.module)
                const state = data.result.parameters.command === 'turn on' || data.result.parameters.command === 'ligar'
                this.$store.dispatch('UPDATE_MODULE_STATE', { identifier: module.identifier, state: state, remote: false })
              }
            }
            speech.speak(data.result.fulfillment.speech)
          })
        })
      }
    }
  }
</script>

<style>

</style>



