import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

import MainLayout from '../layouts/MainLayout'

import DashboardView from '../views/DashboardView'
import LoginView from '../views/LoginView'
import RegisterView from '../views/RegisterView'
import ProfileView from '../views/ProfileView'
import AssociatesView from '../views/AssociatesView'

Vue.use(Router)

const noAuthPaths = [
  '/signin',
  '/signup'
]

const requireAuth = (to, from, next) => {
  if (noAuthPaths.indexOf(to.path) !== -1 || store.state.user.token) {
    next()
  } else {
    next('/signin')
  }
}

const router = new Router({
  routes: [
    {
      path: '/',
      component: MainLayout,
      children: [
        { path: '', component: DashboardView },
        { path: 'profile', component: ProfileView },
        { path: 'associates', component: AssociatesView }
      ]
    },
    { path: '/signin', component: LoginView },
    { path: '/signup', component: RegisterView }
  ]
})

router.beforeEach(requireAuth)

export default router



