import { parseJwt, isExpired } from '../utils/jwt'

let user = localStorage.getItem('user')

if (user) {
  user = JSON.parse(user)

  const payload = user ? parseJwt(user.token) : {}
  const expired = user ? isExpired(user.token) : false

  user = {
    ...user,
    name: expired ? payload.username : '',
    email: expired ? payload.email : '',
    isOwner: expired ? payload.is_owner : ''
  }
} else {
  user = {
    name: '',
    token: '',
    email: '',
    firstName: '',
    lastName: '',
    isOwner: ''
  }
}

export default {
  user: {...user},
  rooms: [],
  modulesState: [],
  changedModule: {
    identifier: '',
    state: null
  },
  editSelectedModule: null
}



