import defaultState from './defaultState'
import Vue from 'vue'
import { speak } from '../utils/speech'

export default {
  SET_ROOMS: (state, rooms) => {
    state.rooms = rooms
  },
  ADD_MODULE_STATE: (state, moduleState) => {
    state.modulesState.push(moduleState)
  },
  SET_MODULE_STATE: (state, moduleState) => {
    const { room, module } = state.rooms.reduce((obj, next) => {
      let m = next.modules.find(_ => _.identifier === moduleState.identifier)
      if (m) {
        return { room: next, module: m }
      }
      return obj
    }, {
      room: null,
      module: null
    })

    if (moduleState.remote && state.modulesState.find((ms) => ms.identifier === moduleState.identifier).state !== moduleState.state) speak(`The ${module.name} was remotely ${moduleState.state ? 'turned on' : 'turned off'} in the ${room.name}`)

    state.changedModule = moduleState
    state.modulesState.find((ms) => ms.identifier === moduleState.identifier).state = moduleState.state
  },
  SET_PROFILE: (state, profile) => {
    state.user.name = profile.username
    state.user.email = profile.email
    state.user.firstName = profile.first_name
    state.user.lastName = profile.last_name
    localStorage.setItem('user', JSON.stringify(state.user))
  },
  SET_USER: (state, user) => {
    state.user = user
    localStorage.setItem('user', JSON.stringify(state.user))
  },
  RESET_STORE: (state) => {
    for (let prop in state) {
      if (prop !== 'route') {
        Vue.set(state, prop, defaultState[prop])
      }
    }
  },
  SET_SELECTED_EDIT_MODULE: (state, module) => {
    state.editSelectedModule = module
  }
}



