<template>
  <section>
    <header-title title="Associates" subtitle="Manage your associates users"/>
    <div class="container">
      <associates-table :associates="associates" />
      <a class="button is-primary" @click="openNewAssociateModal">New Associate</a>
    </div>
    <b-loading :active.sync="isLoading"></b-loading>
    <b-modal :active.sync="isNewAssociateModalActive" has-modal-card>
      <new-associate-form />
    </b-modal>
  </section>
</template>

<script>
import { mapState } from 'vuex'
import api from '../api'
import HeaderTitle from '../components/HeaderTitle'
import NewAssociateForm from '../components/NewAssociateForm'
import AssociatesTable from '../components/AssociatesTable'

export default {
  name: 'associated-view',
  components: { HeaderTitle, NewAssociateForm, AssociatesTable },
  data () {
    return {
      loadingModules: true,
      isLoading: false,
      isNewAssociateModalActive: false,
      associates: []
    }
  },
  computed: mapState(['user', 'rooms', 'modulesState', 'changedModule']),
  watch: {
  },
  beforeMount () {
    api.loadAssociates().then((result) => {
      this.associates = result.data.results
    }).catch((error) => {
      console.log(error)
    })
  },
  methods: {
    openNewAssociateModal () {
      this.isNewAssociateModalActive = true
    }
  }
}
</script>

<style>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #35495E;
}
</style>



